import axios from 'axios';
export const ReportTypePaths = {
    ["cost" /* ReportType.cost */]: 'reports/openshift/infrastructures/azure/costs/',
    ["database" /* ReportType.database */]: 'reports/openshift/infrastructures/azure/costs/',
    ["network" /* ReportType.network */]: 'reports/openshift/infrastructures/azure/costs/',
    ["storage" /* ReportType.storage */]: 'reports/openshift/infrastructures/azure/storage/',
    ["instance_type" /* ReportType.instanceType */]: 'reports/openshift/infrastructures/azure/instance-types/',
};
export function runReport(reportType, query) {
    const path = ReportTypePaths[reportType];
    return axios.get(`${path}?${query}`);
}
