import { getQueryForWidget, getQueryForWidgetTabs, ociDashboardDefaultFilters, ociDashboardStateKey, ociDashboardTabFilters, } from './ociDashboardCommon';
export const selectOciDashboardState = (state) => state[ociDashboardStateKey];
export const selectWidgets = (state) => selectOciDashboardState(state).widgets;
export const selectWidget = (state, id) => selectWidgets(state)[id];
export const selectCurrentWidgets = (state) => selectOciDashboardState(state).currentWidgets;
export const selectWidgetQueries = (state, id) => {
    const widget = selectWidget(state, id);
    const filter = Object.assign(Object.assign({}, ociDashboardDefaultFilters), (widget.filter ? widget.filter : {}));
    const tabsFilter = Object.assign(Object.assign({}, ociDashboardTabFilters), (widget.tabsFilter ? widget.tabsFilter : {}));
    return {
        previous: getQueryForWidget(Object.assign(Object.assign({}, filter), { time_scope_value: -2 })),
        current: getQueryForWidget(filter),
        forecast: getQueryForWidget({}, { limit: 31 }),
        tabs: getQueryForWidgetTabs(widget, Object.assign(Object.assign({}, tabsFilter), { resolution: 'monthly' })),
    };
};
