import { ociCostOverviewStateKey } from './ociCostOverviewCommon';
export const selectOciCostOverviewState = (state) => state[ociCostOverviewStateKey];
export const selectWidgets = (state) => selectOciCostOverviewState(state).widgets;
export const selectWidget = (state, id) => selectWidgets(state)[id];
export const selectCurrentWidgets = (state) => selectOciCostOverviewState(state).currentWidgets;
