import { stateKey } from './featureFlagsReducer';
export const selectFeatureFlagsState = (state) => state[stateKey];
export const selectIsCurrencyFeatureEnabled = (state) => selectFeatureFlagsState(state).isCurrencyFeatureEnabled;
export const selectIsExcludesFeatureEnabled = (state) => selectFeatureFlagsState(state).isExcludesFeatureEnabled;
export const selectIsExportsFeatureEnabled = (state) => selectFeatureFlagsState(state).isExportsFeatureEnabled;
export const selectIsIbmFeatureEnabled = (state) => selectFeatureFlagsState(state).isIbmFeatureEnabled;
export const selectIsOciFeatureEnabled = (state) => selectFeatureFlagsState(state).isOciFeatureEnabled;
