import axios from 'axios';
// Todo: update to use ibm backend apis when they become available
export const ReportTypePaths = {
    ["cost" /* ReportType.cost */]: 'reports/gcp/costs/',
    ["database" /* ReportType.database */]: 'reports/gcp/costs/',
    ["network" /* ReportType.network */]: 'reports/gcp/costs/',
    ["storage" /* ReportType.storage */]: 'reports/gcp/storage/',
    ["instance_type" /* ReportType.instanceType */]: 'reports/gcp/instance-types/',
};
export function runReport(reportType, query) {
    const path = ReportTypePaths[reportType];
    return axios.get(`${path}?${query}`);
}
