import { ociHistoricalDataStateKey } from './ociHistoricalDataCommon';
export const selectOciHistoricalDataState = (state) => state[ociHistoricalDataStateKey];
export const selectWidgets = (state) => selectOciHistoricalDataState(state).widgets;
export const selectWidget = (state, id) => selectWidgets(state)[id];
export const selectCurrentWidgets = (state) => selectOciHistoricalDataState(state).currentWidgets;
