import axios from 'axios';
export const ReportTypePaths = {
    ["cost" /* ReportType.cost */]: 'reports/openshift/costs/',
    ["cpu" /* ReportType.cpu */]: 'reports/openshift/compute/',
    ["memory" /* ReportType.memory */]: 'reports/openshift/memory/',
    ["volume" /* ReportType.volume */]: 'reports/openshift/volumes/',
};
export function runReport(reportType, query) {
    const path = ReportTypePaths[reportType];
    return axios.get(`${path}?${query}`);
}
