import axios from 'axios';
export const ReportTypePaths = {
    ["cost" /* ReportType.cost */]: 'reports/openshift/infrastructures/all/costs/',
    ["cpu" /* ReportType.cpu */]: 'reports/openshift/compute/',
    ["database" /* ReportType.database */]: 'reports/openshift/infrastructures/all/costs/',
    ["instance_type" /* ReportType.instanceType */]: 'reports/openshift/infrastructures/all/instance-types/',
    ["memory" /* ReportType.memory */]: 'reports/openshift/memory/',
    ["network" /* ReportType.network */]: 'reports/openshift/infrastructures/all/costs/',
    ["storage" /* ReportType.storage */]: 'reports/openshift/infrastructures/all/storage/',
    ["volume" /* ReportType.volume */]: 'reports/openshift/volumes/',
};
export function runReport(reportType, query) {
    const path = ReportTypePaths[reportType];
    return axios.get(`${path}?${query}`);
}
