import { getType } from 'typesafe-actions';
import { resetState, setFeatureFlags } from './featureFlagsActions';
export const defaultState = {
    isCurrencyFeatureEnabled: false,
    isExcludesFeatureEnabled: false,
    isExportsFeatureEnabled: false,
    isIbmFeatureEnabled: false,
    isOciFeatureEnabled: false,
};
export const stateKey = 'featureFlags';
export function featureFlagsReducer(state = defaultState, action) {
    switch (action.type) {
        case getType(resetState):
            state = defaultState;
            return state;
        case getType(setFeatureFlags):
            return Object.assign(Object.assign({}, state), { isCurrencyFeatureEnabled: action.payload.isCurrencyFeatureEnabled, isExcludesFeatureEnabled: action.payload.isExcludesFeatureEnabled, isExportsFeatureEnabled: action.payload.isExportsFeatureEnabled, isIbmFeatureEnabled: action.payload.isIbmFeatureEnabled, isOciFeatureEnabled: action.payload.isOciFeatureEnabled });
        default:
            return state;
    }
}
