import { getQuery } from 'api/queries/ociQuery';
export const ociDashboardStateKey = 'ociDashboard';
export const ociDashboardDefaultFilters = {
    time_scope_units: 'month',
    time_scope_value: -1,
    resolution: 'daily',
};
export const ociDashboardTabFilters = Object.assign(Object.assign({}, ociDashboardDefaultFilters), { limit: 3 });
export function getGroupByForTab(widget) {
    switch (widget.currentTab) {
        case "product_services" /* OciDashboardTab.product_services */:
            return {
                product_service: widget.tabsFilter && widget.tabsFilter.product_service ? widget.tabsFilter.product_service : '*',
            };
        case "payer_tenant_ids" /* OciDashboardTab.payer_tenant_ids */:
            return { payer_tenant_id: '*' };
        case "regions" /* OciDashboardTab.regions */:
            return { region: '*' };
        default:
            return {};
    }
}
export function getQueryForWidget(filter = ociDashboardDefaultFilters, props) {
    const query = Object.assign({ filter }, (props ? props : {}));
    return getQuery(query);
}
export function getQueryForWidgetTabs(widget, filter = ociDashboardDefaultFilters) {
    const group_by = getGroupByForTab(widget);
    const newFilter = Object.assign({}, JSON.parse(JSON.stringify(filter)));
    if (widget.currentTab === "product_services" /* OciDashboardTab.product_services */ &&
        widget.tabsFilter &&
        widget.tabsFilter.product_service) {
        newFilter.service = undefined;
    }
    const query = {
        filter: newFilter,
        group_by,
    };
    return getQuery(query);
}
